.watch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.watch__circle {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  display: flex;
  position: absolute;
}

.watch__seconds {
  z-index: 2;
  transform-origin: bottom;
  background-color: #2c8000;
  justify-content: center;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotation;
  display: flex;
  position: absolute;
  top: 2vmin;
}

.watch__minute {
  transform-origin: bottom;
  background-color: #0700ff;
  justify-content: center;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotation;
  display: flex;
  position: absolute;
  top: 20vmin;
}

@keyframes rotation {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.body {
  justify-content: center;
  align-items: center;
  display: flex;
}

